body {
  background: #D0D0D0;
  font-family: sans-serif;
  margin: 20px;
  padding: 0;
}

.heading {
  background: white;
  /* border: 2px solid green; */
  text-align: center;
  font-size: 70px;
  font-weight: bold;
  margin: -20px;
  margin-bottom: 20px;

}

.board {
  margin: auto;
  width: fit-content;
  padding: 10px;
}

.square {
  background: white;
  border: 2px solid black;
  float: left;
  font-size: 100px;
  font-weight: bold;
  line-height: 34px;
  height: 125px;
  padding: 0;
  text-align: center;
  width: 125px;
  border-radius: 5px;
  color: white;
}

.O {
  background: #13ACFE;
}

.X {
  background: #FF4B60;
}

.board-row:after {
  clear: both;
  content: '';
  display: table;
}

.status {
  padding-top: 20px;
  height: 100px;
  width: 372.5px;
  font-size: 50px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 20px;
  color: white;
  border-radius: 5px;
}

.gameOver {
  color: black;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}